import {Injectable} from '@angular/core';
import {RestService} from './rest.service';
import {Observable} from 'rxjs';
import {IProductItem} from '../models/product.model';
import {ICategory} from '../models/category.model';

@Injectable()
export class CatalogService {

  constructor(private readonly restService: RestService) {

  }

  getCategories(): Observable<ICategory[]> {
    return this.restService.get(`categories`);
  }

  getProduct(id: number): Observable<IProductItem> {
    return this.restService.get(`product?${id}`);
  }

  getProductsByCategory(categoryId: number, pageIndex: number = 0, pageSize: number = 8): Observable<IProductItem[]> {
    return this.restService.get(`/catalog/categoriesItems?categoryId=${categoryId}&pageIndex=${pageIndex}&pageSize=${pageSize}`);
  }

}
