import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';

import {Observable, throwError} from 'rxjs';
import {catchError, tap} from 'rxjs/operators';

import {StorageService} from './storage.service';
import {Guid} from '../../../guid';

@Injectable()
export class RestService {
  constructor(
    private readonly http: HttpClient,
    private readonly storageService: StorageService) {
  }

  get(url: string, params?: any): Observable<any> {
    const options = {};
    this.setHeaders(options, true);

    return this.http.get(url, options)
      .pipe(
        catchError(this.handleError)
      );
  }

  postNoContentType(url: string, data: any, params?: {}): Observable<any> {
    return this.doPost(url, data, false, false, params);
  }

  postWithId(url: string, data: any, params?: {}): Observable<any> {
    return this.doPost(url, data, true, true, params);
  }

  post(url: string, data: any, params?: {}): Observable<any> {
    return this.doPost(url, data, true, false, params);
  }

  putWithId(url: string, data: any, params?: {}): Observable<any> {
    return this.doPut(url, data, true, true, params);
  }

  put(url: string, data: any, params?: {}): Observable<any> {
    return this.doPut(url, data, true, false, params);
  }

  private doPost(url: string, data: any, needHeaders: boolean, needId: boolean, params?: {}): Observable<any> {
    const options = {};
    if (params) {
      for (const [key, value] of Object.entries(params)) {
        options[key] = value;
      }
    }
    this.setHeaders(options, needHeaders, needId);
    return this.http.post(url, data, options)
      .pipe(
        catchError(this.handleError)
      );
  }

  private doPut(url: string, data: any, needHeaders: boolean, needId: boolean, params?: any): Observable<any> {
    const options = {};
    this.setHeaders(options, needHeaders, needId);
    return this.http.put(url, data, options)
      .pipe(
        catchError(this.handleError)
      );
  }

  delete(url: string, params?: any): Observable<any> {
    const options = {};
    this.setHeaders(options, true);

    console.log('data.service deleting');

    return this.http.delete(url, options)
      .pipe(
        catchError(this.handleError)
      );
  }

  // Удалить сущности по идентфикаторам
  deleteManyElements(url: string, ids: any): Observable<Response> {
    const urlParamsArray: string[] = [];
    for (const id of ids) {
      urlParamsArray.push(`ids=${id}`);
    }
    let urlParamsString = urlParamsArray.join('&');
    urlParamsString = `?${urlParamsString}`;

    const options = {};
    this.setHeaders(options, true);

    return this.http.delete(url + urlParamsString, options)
      .pipe(
        tap((res: Response) => {
          return res;
        }),
        catchError(this.handleError));
  }

  private handleError(error: any) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('Client side network error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.log(error);
      console.error('Backend - ' +
        `status: ${error.status}, ` +
        `statusText: ${error.statusText}, ` +
        `message: ${error.error.message}`);
    }

    // return an observable with a user-facing error message
    return throwError(error || 'server error');
  }

  private setHeaders(options: any, needHeaders: boolean, needId?: boolean): void {
    if (needId) {
      this.configureAuth(options, needHeaders, true);
    } else {
      this.configureAuth(options, needHeaders);
    }
  }

  private configureAuth(opts: any, headersNeed: boolean, guidNeed: boolean = false): void {
    const tokens = this.storageService.getNoParse('auth-tokens');
    if (tokens != null) {
      const auth = JSON.parse(tokens);
      console.groupCollapsed('%c Auth Http Service:', 'font-size: 14px; font-weight: 400; color: gray');
      console.log(auth);
      console.groupEnd();
      if (auth.access_token != null) {
        if (headersNeed === false) {
          opts.headers = new HttpHeaders();
        } else if (opts.headers == null) {
          opts.headers = new HttpHeaders({
            'Content-Type': 'application/json',
            'Accept': 'application/json'
          });
        }
        opts.headers = opts.headers.append('Authorization', `Bearer ${auth.access_token}`);
        if (guidNeed) {
          opts.headers = opts.headers.append('x-requestid', Guid.newGuid());
        }
      }
    }
  }
}
