import {ModuleWithProviders, NgModule} from '@angular/core';
import {MatDialogModule} from '@angular/material/dialog';
import {IconSpriteModule} from 'ng-svg-icon-sprite';
import {CommonModule} from '@angular/common';
import {RouterModule} from '@angular/router';
import {ReactiveFormsModule} from '@angular/forms';
import {SiteLayoutComponent} from './layouts/site-layout/site-layout.component';
import {ModalAddressComponent} from './components/modal-address/modal-address.component';
import {BasketService} from './services/basket.service';
import {AddressService} from './services/address.service';
import {RestService} from './services/rest.service';
import {StorageService} from './services/storage.service';
import {CatalogService} from './services/catalog.service';
import {BasketItemComponent} from '../basket/basket-item/basket-item.component';
import {CounterComponent} from './components/counter/counter.component';
import {BasketComponent} from '../basket/basket.component';
import {LineLimitComponent} from './components/line-limit/line-limit.component';
import {BasketTotalComponent} from '../basket/basket-total/basket-total.component';
import {MatCheckboxModule} from '@angular/material/checkbox';
import {MatButtonModule} from '@angular/material/button';

@NgModule({
  imports: [
    IconSpriteModule,
    CommonModule,
    RouterModule,
    ReactiveFormsModule,
    MatCheckboxModule,
    MatDialogModule,
    MatButtonModule
  ],
  declarations: [
    SiteLayoutComponent,
    ModalAddressComponent,
    BasketItemComponent,
    CounterComponent,
    BasketComponent,
    LineLimitComponent,
    BasketTotalComponent
  ],
  exports: [
    IconSpriteModule,
    CommonModule,
    ReactiveFormsModule,
    BasketItemComponent,
    BasketTotalComponent
  ]
})
export class SharedModule {
  static forRoot(): ModuleWithProviders<SharedModule> {
    return {
      ngModule: SharedModule,
      providers: [
        // Providers
        RestService,
        StorageService,
        BasketService,
        AddressService,
        CatalogService
      ]
    };
  }
}
